import Grid from "@mui/material/Grid";

// Material Dashboard 2 React components
import MDBox from "components/MDBox";

// Material Dashboard 2 React example components
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import ReportsLineChart from "examples/Charts/LineCharts/ReportsLineChart";
import ComplexStatisticsCard from "examples/Cards/StatisticsCards/ComplexStatisticsCard";
// Data
// import reportsLineChartData from "layouts/dashboard/data/reportsLineChartData";
import { useState, useEffect } from "react";
import axios from "axios";

function Dashboard() {
  const [co2, Setco2] = useState("");
  const [voc, Setvoc] = useState("");
  const [x, Setx] = useState(0);
  const [pm10, Setpm10] = useState("");
  const [pm25, Setpm25] = useState("");
  const [sales, Setsales] = useState({});
  const [tasks, Settasks] = useState({});
  const [pert, Setpert] = useState({});
  const [VOC, SetVOC] = useState({});
  const [co2color, Setco2color] = useState("");
  const [pm25color, Setpm25color] = useState("");
  const [pm10color, Setpm10color] = useState("");
  const [voccolor, Setvoccolor] = useState("");
  const ll = {
    // console.log(pm10, "hhhhhhhhh");
    sales: {
      labels: [],
      datasets: { label: "PM 2.5 READING", data: [] },
    },
    pert: {
      labels: [],
      datasets: { label: "CO2 READING", data: [] },
    },
    VOC: {
      labels: [],
      datasets: {
        label: "  ",
        data: [],
      },
    },
    tasks: {
      labels: [],
      datasets: {
        label: "PM 10 READING",
        data: [],
      },
    },
  };

  // const { sales, tasks, pert, VOC } = reportsLineChartData;

  for (let i = 0; i < 20; i += 1) {
    ll.sales.labels.push("");
    ll.sales.datasets.data.push(0);
    ll.tasks.labels.push("");
    ll.tasks.datasets.data.push(0);
    ll.pert.labels.push("");
    ll.pert.datasets.data.push(0);
    ll.VOC.labels.push("");
    ll.VOC.datasets.data.push(0);
  }

  // Setkk(ll);

  useEffect(() => {
    // const { sales, tasks, pert, VOC } = reportsLineChartData;
    axios
      .get(
        "https://api.thingspeak.com/channels/1847325/fields/1.json?api_key=RAQG0XAVFW0D587B&results=20"
      )
      .then((res) => {
        const imp1data = res.data;
        Setco2(imp1data.feeds[imp1data.feeds.length - 1].field1);

        if (Number(co2) < 600) {
          Setco2color("success");
        } else if (Number(co2) < 800) {
          Setco2color("warning");
        } else {
          Setco2color("error");
        }

        // Setkk(ll);
      });

    axios
      .get(
        "https://api.thingspeak.com/channels/1847325/fields/2.json?api_key=RAQG0XAVFW0D587B&results=20"
      )
      .then((res) => {
        const imp2data = res.data;
        Setvoc(imp2data.feeds[imp2data.feeds.length - 1].field2);

        if (Number(voc) < 100) {
          Setvoccolor("success");
        } else if (Number(voc) < 250) {
          Setvoccolor("warning");
        } else {
          Setvoccolor("error");
        }
      });
    axios
      .get(
        "https://api.thingspeak.com/channels/1847325/fields/3.json?api_key=RAQG0XAVFW0D587B&results=20"
      )
      .then((res) => {
        const imp3data = res.data;
        Setpm25(imp3data.feeds[imp3data.feeds.length - 1].field3);
        if (Number(pm25) < 40) {
          Setpm25color("success");
        } else if (Number(pm25) < 100) {
          Setpm25color("warning");
        } else {
          Setpm25color("error");
        }
      });
    axios
      .get(
        "https://api.thingspeak.com/channels/1847325/fields/4.json?api_key=RAQG0XAVFW0D587B&results=20"
      )
      .then((res) => {
        const imp4data = res.data;
        Setpm10(imp4data.feeds[imp4data.feeds.length - 1].field4);
        if (Number(pm10) < 40) {
          Setpm10color("success");
        } else if (Number(pm10) < 100) {
          Setpm10color("warning");
        } else {
          Setpm10color("error");
        }
        if (x === 0) {
          Setx(1);
        } else {
          Setx(0);
        }
      });
  }, [x]);

  useEffect(() => {
    // const { sales, tasks, pert, VOC } = reportsLineChartData;
    axios
      .get(
        "https://api.thingspeak.com/channels/1847325/fields/1.json?api_key=RAQG0XAVFW0D587B&results=20"
      )
      .then((res) => {
        const imp1data = res.data;
        let ii;
        for (let i = 0; i < imp1data.feeds.length; i += 1) {
          ii = String(imp1data.feeds[i].created_at);
          const s = new Date(ii).toLocaleTimeString(undefined, { timeZone: "Asia/Kolkata" });
          // const s = new Date(ii).toLocaleString(undefined, { timeZone: "Asia/Kolkata" });
          // const k = new Date(s);
          // const ss = k.getDate();
          ll.pert.labels[i] = s;
          ll.pert.datasets.data[i] = Number(imp1data.feeds[i].field1);
        }
        Setpert(ll.pert);
        // Setkk(ll);
      });
    axios
      .get(
        "https://api.thingspeak.com/channels/1847325/fields/2.json?api_key=RAQG0XAVFW0D587B&results=20"
      )
      .then((res) => {
        const imp2data = res.data;
        let ii;
        for (let i = 0; i < imp2data.feeds.length; i += 1) {
          ii = String(imp2data.feeds[i].created_at);
          const s = new Date(ii).toLocaleTimeString(undefined, { timeZone: "Asia/Kolkata" });
          // const ss = new Date(s).toLocaleTimeString();
          ll.VOC.labels[i] = s;
          ll.VOC.datasets.data[i] = Number(imp2data.feeds[i].field2);
        }
        SetVOC(ll.VOC);
      });
    axios
      .get(
        "https://api.thingspeak.com/channels/1847325/fields/3.json?api_key=RAQG0XAVFW0D587B&results=20"
      )
      .then((res) => {
        const imp3data = res.data;
        let ii;
        for (let i = 0; i < imp3data.feeds.length; i += 1) {
          ii = String(imp3data.feeds[i].created_at);
          const s = new Date(ii).toLocaleTimeString(undefined, { timeZone: "Asia/Kolkata" });
          // const ss = new Date(s).toLocaleTimeString();
          ll.sales.labels[i] = s;
          ll.sales.datasets.data[i] = Number(imp3data.feeds[i].field3);
        }
        Setsales(ll.sales);
      });
    axios
      .get(
        "https://api.thingspeak.com/channels/1847325/fields/4.json?api_key=RAQG0XAVFW0D587B&results=20"
      )
      .then((res) => {
        const imp4data = res.data;
        let ii;
        for (let i = 0; i < imp4data.feeds.length; i += 1) {
          ii = String(imp4data.feeds[i].created_at);
          const s = new Date(ii).toLocaleTimeString(undefined, { timeZone: "Asia/Kolkata" });
          // const ss = new Date(s).toLocaleTimeString();
          ll.tasks.labels[i] = s;
          ll.tasks.datasets.data[i] = Number(imp4data.feeds[i].field4);
        }
        Settasks(ll.tasks);
        if (x === 0) {
          Setx(1);
        } else {
          Setx(0);
        }
      });
  }, [co2]);
  return (
    <DashboardLayout>
      <DashboardNavbar />
      <MDBox py={3}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={6} lg={3}>
            <MDBox mb={1.5}>
              <ComplexStatisticsCard
                color={co2color}
                icon="co2"
                title="CO2"
                count={co2}
                percentage={{ label: "PPM" }}
              />
            </MDBox>
          </Grid>
          <Grid item xs={12} md={6} lg={3}>
            <MDBox mb={1.5}>
              <ComplexStatisticsCard
                color={pm25color}
                icon="air"
                title="PM 2.5 (Dust)"
                count={pm25}
                percentage={{
                  color: "dark",
                  label: "PPM",
                }}
              />
            </MDBox>
          </Grid>
          <Grid item xs={12} md={6} lg={3}>
            <MDBox mb={1.5}>
              <ComplexStatisticsCard
                color={pm10color}
                icon="masks"
                title="PM 10 (Dust)"
                count={Number(pm10)}
                percentage={{
                  color: "dark",
                  label: "PPM",
                }}
              />
            </MDBox>
          </Grid>
          <Grid item xs={12} md={6} lg={3}>
            <MDBox mb={1.5}>
              <ComplexStatisticsCard
                color={voccolor}
                icon="factory"
                title="VOC"
                count={voc}
                percentage={{
                  color: "dark",
                  label: "VOC index",
                }}
              />
            </MDBox>
          </Grid>
        </Grid>
        <MDBox mt={4.5}>
          <Grid container spacing={3}>
            <Grid item xs={12} md={6} lg={6}>
              <MDBox mb={3}>
                <ReportsLineChart color="dark" title="CO2" date="CO2 Graph Readings" chart={pert} />
              </MDBox>
            </Grid>
            <Grid item xs={12} md={6} lg={6}>
              <MDBox mb={3}>
                <ReportsLineChart
                  color="secondary"
                  title="PM 2.5 (Dust)"
                  date="PM 2.5 Graph Readings"
                  chart={sales}
                />
              </MDBox>
            </Grid>
            <Grid item xs={12} md={6} lg={6}>
              <MDBox mb={3}>
                <ReportsLineChart
                  color="primary"
                  title="VOC"
                  date="VOC Raw Graph Readings"
                  chart={VOC}
                />
              </MDBox>
            </Grid>
            <Grid item xs={12} md={6} lg={6}>
              <MDBox mb={3}>
                <ReportsLineChart
                  color="success"
                  title="PM 10 (Dust)"
                  date="PM 10 Graph Readings"
                  chart={tasks}
                />
              </MDBox>
            </Grid>
          </Grid>
        </MDBox>
      </MDBox>
    </DashboardLayout>
  );
}

export default Dashboard;
